import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-default-button2',
  templateUrl: './default-button2.component.html',
  styleUrls: ['./default-button2.component.scss']
})
export class DefaultButton2Component implements OnInit {

  @Input() text: string;
  @Input() height: number;
  @Input() width: number;
  @Output() buttonClick = new EventEmitter();

  constructor() {
    this.text = "Button";
    this.height = 26;
    this.width = 76;
  }

  ngOnInit(): void {
  }

  onClick() {
    this.buttonClick.emit();
  }

}
